import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom'; // react-router-dom v6 
import './Login.css';

function Login({ toggleForm }) {
  const [team, setTeam] = useState('');
  const [id, setId] = useState('');
  const [password, setPassword] = useState('');
  const navigate = useNavigate();

  const handleTeamChange = (newTeam) => {
    setTeam(newTeam);
  };

  const handleLogin = async () => {
    try {
      const response = await axios.get('/login', { // URL '/login' 
        params: {
          id,
          password,
          team
        }
      });

      if (response.data.success) {
        //  
        navigate('/map'); // navigate 
      } else {
        if (response.data.message === 'User not found') {
          alert('  ');
        } else if (response.data.message === 'Incorrect password') {
          alert(' ');
        }
      }
    } catch (error) {
      console.error('Login error', error);
      alert('   ');
    }
  };

  return (
    <div className="login-container">
      <div className="logo">
        <img src="/path/to/logo.png" alt="" /> {/*    */}
      </div>
      <div className="team-selector">
        <button
          className={team === '' ? 'active' : ''}
          onClick={() => handleTeamChange('')}
        >
          
        </button>
        <button
          className={team === '' ? 'active' : ''}
          onClick={() => handleTeamChange('')}
        >
          
        </button>
      </div>
      <div className="login-form">
        <div className="idbox">ID</div>
        <input 
          type="text" 
          placeholder="ID" 
          value={id}
          onChange={(e) => setId(e.target.value)}
        />
        <div className="psbox">Password</div>
        <input 
          type="password" 
          placeholder="Password" 
          value={password}
          onChange={(e) => setPassword(e.target.value)}
        />
        <button onClick={handleLogin}></button>
        <div className="register-link">
          <span onClick={toggleForm}></span>
        </div>
      </div>
    </div>
  );
}

export default Login;
