import React, { useState } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import Navbar from './components/Navbar';
import Login from './components/Login';
import Register from './components/Register';
import MainPage from './pages/MainPage';
import StatsPage from './pages/StatsPage';
import ReportPage from './pages/ReportPage';
import ReportAfterPage from './pages/ReportAfterPage';
import BeforeLink from './pages/BeforeLink';
import AfterLink from './pages/AfterLink';
import LinkPage from './pages/LinkPage';
import LogoutPage from './pages/LogoutPage';
import './App.css';

const App = () => {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [isRegister, setIsRegister] = useState(false);

  const toggleForm = () => {
    setIsRegister(!isRegister);
  };

  const handleLogin = () => {
    setIsAuthenticated(true);
  };

  const handleLogout = () => {
    setIsAuthenticated(false);
  };

  return (
    <Router>
      {isAuthenticated && <Navbar onLogout={handleLogout} />}
      <div style={{ paddingTop: isAuthenticated ? '80px' : '0' }}>
        <Routes>
          {isAuthenticated ? (
            <>
              <Route path="/" element={<MainPage />} />
              <Route path="/stats" element={<StatsPage />} />
              <Route path="/report" element={<ReportPage />} />
              <Route path="/report/after" element={<ReportAfterPage />} />
              <Route path="/link/*" element={<LinkPage />}>
                <Route path="" element={<Navigate to="before" replace />} />
                <Route path="before" element={<BeforeLink />} />
                <Route path="after" element={<AfterLink />} />
              </Route>
              <Route path="/logout" element={<LogoutPage onLogout={handleLogout} />} />
            </>
          ) : (
            <>
              {isRegister ? (
                <Route path="/" element={<Register toggleForm={toggleForm} />} />
              ) : (
                <Route path="/" element={<Login toggleForm={toggleForm} onLogin={handleLogin} />} />
              )}
              <Route path="*" element={<Navigate to="/" replace />} />
            </>
          )}
        </Routes>
      </div>
    </Router>
  );
};

export default App;
